<template>
  <ion-page>
    <ion-header>
      <ion-toolbar style="background-color: white;">
        <ion-title>Tanya.Ai</ion-title>
        <ion-back-button slot="start" default-href="/" />
        <ion-buttons slot="end">
          <ion-button @click="openMenu($event)">
            <ion-icon :icon="ellipsisVertical"></ion-icon>
          </ion-button>
        </ion-buttons>
      </ion-toolbar>
    </ion-header>

    <!-- Popover untuk Hapus Chat -->
    <ion-popover :is-open="isMenuOpen" :event="menuEvent" @didDismiss="isMenuOpen = false">
      <ion-content>
        <ion-list>
          <ion-item button @click="clearChat">
            <ion-label>Hapus Chat</ion-label>
          </ion-item>
        </ion-list>
      </ion-content>
    </ion-popover>

    <ion-content class="ion-padding chat-container">
      <!-- Daftar Pesan -->
      <div v-for="(msg, index) in messages" :key="index" 
           class="message-container" 
           :class="{'user-message': msg.startsWith(''), 'ai-message': msg.startsWith('')}">
        <div class="message-bubble">
          <ion-text>
            <span v-for="(line, i) in msg.split('\n')" :key="i" class="chat-line">
              {{ line }}<br>
            </span>
          </ion-text>

          <!-- Tombol Report jika pesan dari AI -->
          <div v-if="msg.startsWith('')" style="margin-top: 4px;">
            <ion-badge color="danger" @click="openReportModal(index)" class="report-badge">
              Report Feedback
            </ion-badge>
          </div>
        </div>
        
      </div>

      <!-- Indikator Loading -->
      <div v-if="isLoading" class="message-container ai-message">
        <div class="message-bubble loading-bubble">
          <ion-text> Joni sedang mengetik...</ion-text>
        </div>
      </div>
    </ion-content>


    <ion-modal :is-open="showReportModal" @didDismiss="showReportModal = false">
  <ion-header>
    <ion-toolbar>
      <ion-title>Report Message</ion-title>
      <ion-buttons slot="end">
        <ion-button @click="showReportModal = false">
          <p>Close</p>
        </ion-button>
      </ion-buttons>
    </ion-toolbar>
  </ion-header>
  <ion-content class="ion-padding">
    <ion-item>
      <ion-label position="stacked">Why do you want to report this message?</ion-label>
      <ion-textarea v-model="reportText" placeholder="Explain the issue..."></ion-textarea>
    </ion-item>
    <ion-button expand="block" color="danger" @click="submitReport">Submit Report</ion-button>
  </ion-content>
</ion-modal>

    <!-- Input dan Tombol Kirim -->
    <ion-footer>
      <ion-toolbar>
        <ion-grid>
          <ion-row class="ion-align-items-center">
            <ion-col size="10">
              <ion-textarea
                v-model="userInput"
                :disabled="isLoading"
                placeholder="Tanyakan sesuatu tentang pendidikan..."
                auto-grow
                class="chat-input"
                @keydown.enter.exact.prevent="handleEnter"
                @keydown.shift.enter="newLine"
              ></ion-textarea>
            </ion-col>
            <ion-col size="2" class="ion-text-center">
              <ion-button expand="full" @click="askAI" color="primary" :disabled="isLoading">
                <img src="../assets/send.png" alt="Send">
              </ion-button>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-toolbar>
    </ion-footer>
  </ion-page>
</template>

<script>
import { 
  IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonText, IonTextarea, IonButton, 
  IonFooter, IonGrid, IonRow, IonCol, IonPopover, IonItem, IonLabel, IonList, IonButtons, 
  IonIcon, IonBackButton, IonModal
} from "@ionic/vue";
import { ellipsisVertical } from "ionicons/icons";
import axios from "axios";

export default {
  components: {
    IonPage,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonText,
    IonTextarea,
    IonButton,
    IonFooter,
    IonGrid,
    IonRow,
    IonCol,
    IonPopover,
    IonItem,
    IonLabel,
    IonList,
    IonButtons,
    IonIcon,
    IonBackButton,
    IonModal,
  },
  data() {
    return {
      userInput: "",
      messages: [],
      isLoading: false,
      isMobile: false,
      ellipsisVertical,
      isMenuOpen: false,
      menuEvent: null,
      showReportModal: false,
      reportText: "",
      reportedMessageIndex: null,
    };
  },
  mounted() {
    this.isMobile = /Android|iPhone|iPad|iPod/i.test(navigator.userAgent);
    this.loadChat();
  },
  methods: {
    openMenu(event) {
      this.menuEvent = event;
      this.isMenuOpen = true;
    },
    handleEnter(event) {
      if (this.isMobile) {
        event.preventDefault();
        this.newLine();
      } else {
        this.askAI();
      }
    },
    newLine() {
      this.userInput += "\n";
    },
    saveChat() {
      localStorage.setItem("chatMessages", JSON.stringify(this.messages));
    },
    loadChat() {
      const savedMessages = localStorage.getItem("chatMessages");
      if (savedMessages) {
        this.messages = JSON.parse(savedMessages);
      }
    },
    clearChat() {
      this.messages = [];
      localStorage.removeItem("chatMessages");
      this.isMenuOpen = false;
    },
    async askAI() {
      if (!this.userInput.trim()) return;

      // Simpan pesan user & hapus input
      const userMessage = ": " + this.userInput;
      this.messages.push(userMessage);
      this.userInput = "";  // Hapus input setelah dikirim
      this.isLoading = true; // Kunci input & tombol kirim

      this.saveChat();

      const apiKey = "AIzaSyCL5u-B1jR7JYqOiKkmL1rEoRdXTLMuvKk";
      const apiUrl = `https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent?key=${apiKey}`;

      try {
        const requestBody = {
          contents: [{ parts: [{ text: `Joni is an AI assistant specialized in education. Answer this question: ${userMessage}` }] }],
        };

        const response = await axios.post(apiUrl, requestBody, { headers: { "Content-Type": "application/json" } });
        let aiResponse = response.data.candidates?.[0]?.content?.parts?.[0]?.text || "Joni tidak bisa memberikan jawaban terkait pendidikan.";
        aiResponse = aiResponse.replace(/\*/g, "").trim();

        this.messages.push(" Joni: " + aiResponse);
        this.saveChat();
      } catch (error) {
        console.error("Error fetching AI response:", error);
        this.messages.push(" Joni: Maaf, terjadi kesalahan.");
      }

      this.isLoading = false; // Buka kembali input & tombol kirim
    },
    openReportModal(index) {
      this.reportedMessageIndex = index;
      this.reportText = "";
      this.showReportModal = true;
    },

    submitReport() {
      const reportedMsg = this.messages[this.reportedMessageIndex];
      console.log("Reported message:", reportedMsg);
      console.log("Reason:", this.reportText);

      // TODO: Simpan ke server / Firestore jika diinginkan
      alert("Report submitted. Thank you!");

      this.showReportModal = false;
    }
  },
};
</script>

<style scoped>
