}

.ai-message {
  justify-content: flex-start;
}

.ai-message .message-bubble {
  background-color: #ffffff;
  border-radius: 12px 12px 12px 0;
}

/* Loading Style */
.loading-bubble {
  font-style: italic;
  color: gray;
}

.chat-line {
  display: block;
  margin-bottom: 4px;
}

.chat-input {
  background-color: #ffffff;
